import { ReactNode } from 'react';

import {
  StyledFeatureArticle,
  StyledSummary,
  StyledP,
  StyledImg,
  StyledDivWrapper,
} from './InfoBlock.styled';

type InfoBlockProps =  {
  title: string;
  text: string;
  imgSrc: string;
  imgAlt: string;
  idForText?: string;
  children?: ReactNode;
}

const InfoBlock = ({
  title,
  text,
  imgSrc,
  imgAlt,
  idForText,
}: InfoBlockProps): JSX.Element => {

  return (
    <StyledFeatureArticle>
      <details>
        <StyledSummary>{title}</StyledSummary>
        <StyledDivWrapper>
          {!!imgSrc && <StyledImg src={imgSrc} alt={imgAlt} />}
          <StyledP id={idForText}>{text}</StyledP>
        </StyledDivWrapper>
      </details>
    </StyledFeatureArticle>
  );
  
};

export default InfoBlock;
